body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn, input {
  margin-left: 5px;
  margin-right: 5px;
}

#root {
  padding: 16px;
  text-align: center;
}

.btn:first-child, input:first-child {
  margin-left: 0;
}

.btn:last-child, input:last-child {
  margin-right: 0;
}

.instead {
  padding-top: 5px;
  padding-bottom: 5px;
}

.app__row {
  margin-bottom: 15px;
}
